@keyframes project {
  from {
    opacity: 0;
  }

  to {
    opacity: 100;
  }
}

#projects {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
}

#projectsContentContainer {
  display: flex;
  flex-direction: column;
  width: 75%;
  justify-content: center;
}

#projectsHeader {
  width: 80%;
  margin: 30px 0 30px 0;
}

#projectsContainer {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
}


/* Project component css */
.project {
  height: auto;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 30px;

  flex-grow: 1;

  /* animation-name: project;
    animation-duration: 4s; */
}

.projectDetails {
  display: flex;
  flex-direction: column;
  gap: 20px;

  margin: 15px;
  flex: 1 1 auto;
  width: 100%;
}

.projectType {
  font-weight: 230;
}

.projectDate {
  font-weight: 200;
}

.projectTitle {
  font-weight: 300;
  margin: 0;
}

.projectDescription {
  margin: 0;
}

.typeAndDateContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.projectImgContainer {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  flex: 0 1 auto;
  width: 90%;
}

.projectImg {
  width: 100%;
}

.linksContainer {
  display: flex;
  gap: 12px;
}

.writeUpInProgressText {
  font-weight: 200;
  color: var(--red);
}