.captionedImageContainer {
  flex: 1 0 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: solid var(--gray) 0.5px;
  margin-right: 12px;
  padding: 12px;
}

.captionedImage {
  margin: 12px;
  width: 90%;
}

.captionedImageDivider {
  border: solid var(--gray) 0.5px;
  width: 90%;
}

.imgCaption {
  font-size: 12px;
}
