html {
  scroll-behavior: smooth;
}

#container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 100px;
  border-bottom: solid var(--white);
}

#mainContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 40px;
}

#portraitPhoto {
  border-radius: 50%;
  padding: 12px;
  width: 300px;
  height: auto;
}

#aboutMeContainer {
  width: 500px;
}

#arrowContainer {
  position:absolute;
  bottom:0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 20px;
}

#myProjectsText {
  margin: 6px;
}

#linksBar {
  display: flex;
  gap: 20px;
}

.svg {
  width: 24px;
  height: 24px;
}

#projectsAnchor {
  text-align: center;

}

#downArrow {
  width: 15%;
}
